<template>
	<view class="">
		<step-one v-if="one" @onNexts="handleNexts" :phone="phone"></step-one>
		<step-two v-if="two" @paySuccess="HandlePaySuccess" :phoneData="phoneData"></step-two>
		<step-three v-if="three" @onNext="handleNext" :landData="landData"></step-three>
		<step-four v-if="four" @onFinish="handleFinish"></step-four>
	</view>
</template>

<script>
	import StepOne from './components/step-one.vue'
	import StepTwo from './components/step-two.vue'
	import StepThree from './components/step-three.vue'
	import StepFour from './components/step-four.vue'
	import {
		GetUserBuyLandSelectAjax,
		PostFillLandDetailAjax
	} from '@/apis/api/modules/buyland'
	export default {
		components: {
			StepOne,
			StepTwo,
			StepThree,
			StepFour
		},
		data() {
			return {
				one: true,
				two: false,
				three: false,
				four: false,
				// phone: {
				// 	openid: null,
				// 	session_key: null,
				// },
				phone: null,
				phoneData: null,
				landData: {},
				formData: {
					FarmName: "",
					LandID: "",
					SeedlingsID: "",
					Name: "",
					Phone: "",
					Province: "string",
					City: "",
					Region: "",
					Address: ""
				}
			}
		},
		onLoad(e) {
			if (e.isGetPhone === "true") {
				this.phone = this.$store.state.user.userData.Phone
				// 	this.one = false
				// 	this.two = true
			} else {

				this.phone = e.phone
				// this.phone.openid = e.openid
				// this.phone.session_key = decodeURIComponent(e.session_key)
			}
		},
		methods: {
			handleNexts(e) {
				this.one = false
				this.two = true
				this.phoneData = e || this.$store.state.user.userData.Phone
			},
			async HandlePaySuccess(e) {
				this.two = false
				this.three = true
				const result = await GetUserBuyLandSelectAjax({
					orderNo: e,
					token: true
				})
				this.landData = result.Result
				this.formData.LandID = this.landData.LandID
				this.formData.Phone = this.landData.Phone
			},
			handleNext(farmName, seedlingId) {
				this.three = false
				this.four = true
				this.formData.FarmName = farmName
				this.formData.SeedlingsID = seedlingId
			},
			async handleFinish(e) {
				await PostFillLandDetailAjax({
					...this.formData,
					...e,
					token: true
				})
				uni.showToast({
					title: "完善信息成功！",
					icon: 'none'
				})
				setTimeout(() => {
					uni.navigateTo({
						url: '/pages/index/index'
					})
				}, 1000)
			}
		}
	}
</script>

